import React from "react";
import ReactDOM from "react-dom";

const Modal = ({ message, onClose }) => {
  return ReactDOM.createPortal(
    <div className="fixed inset-0 flex justify-center items-center border-transparent bg-black bg-opacity-50 z-50">
      <div className="w-[360px] min-w-[360px] rounded-xl bg-white shadow-lg">
        <div className="p-4 m-3 pb-7 text-base text-darkGray text-center border-b border-lightGray">
          {message}
        </div>
        <div className="h-12 px-2 pb-3 flex justify-center items-center">
          <button
            className="w-full h-full text-base font-semibold text-purple"
            onClick={onClose}
          >
            확인
          </button>
        </div>
      </div>
    </div>,
    document.getElementById("modal-root")
  );
};

export default Modal;
